/**
 * Site header
 */
.site-header {
  border-top: 5px solid $background-color;
  border-bottom: 1px solid $grey-color-light;
  min-height: 56px;

  padding-top: 20px;
  padding-bottom: 20px;

  // Positioning context for the mobile navigation icon
  position: relative;
}

.site-title {
  font-size: 36px;
  font-weight: 300;
  line-height: 56px;  
  margin-bottom: 0;
  text-align: center;

  &,
  &:visited {
    color: $brand-color;
  }

   @include media-query($on-palm) {    
      font-size: 19.2px;   
  }
}

.site-nav {
  text-align: center;
  line-height: 56px;

  .menu-icon {
    display: none;
  }

  .page-link {
    color: $text-color;
    line-height: $base-line-height;

    // Gaps between nav items, but not on the last one
    &:not(:last-child) {
      margin-right: 20px;
    }
  }    
}


/**
 * Site footer
 */
.site-footer {
  border-top: 1px solid $grey-color-light;
  padding: $spacing-unit 0;
}

.footer-heading {
  font-size: 18px;
  margin-bottom: $spacing-unit / 2;
}

.contact-list,
.social-media-list {
  list-style: none;
  margin-left: 0;
}

.footer-col-wrapper {
  font-size: 15px;
  color: $grey-color;
  margin-left: -$spacing-unit / 2;
  @extend %clearfix;
}

.footer-col {
  float: left;
  margin-bottom: $spacing-unit / 2;
  padding-left: $spacing-unit / 2;
}

.footer-col-1 {
  width: -webkit-calc(35% - (#{$spacing-unit} / 2));
  width:         calc(35% - (#{$spacing-unit} / 2));
}

.footer-col-2 {
  width: -webkit-calc(20% - (#{$spacing-unit} / 2));
  width:         calc(20% - (#{$spacing-unit} / 2));
}

.footer-col-3 {
  width: -webkit-calc(45% - (#{$spacing-unit} / 2));
  width:         calc(45% - (#{$spacing-unit} / 2));
}

@include media-query($on-laptop) {
  .footer-col-1,
  .footer-col-2 {
    width: -webkit-calc(50% - (#{$spacing-unit} / 2));
    width:         calc(50% - (#{$spacing-unit} / 2));
  }

  .footer-col-3 {
    width: -webkit-calc(100% - (#{$spacing-unit} / 2));
    width:         calc(100% - (#{$spacing-unit} / 2));
  }
}

@include media-query($on-palm) {
  .footer-col {
    float: none;
    width: -webkit-calc(100% - (#{$spacing-unit} / 2));
    width:         calc(100% - (#{$spacing-unit} / 2));
  }
}



/**
 * Page content
 */
.page-content {
  padding: $spacing-unit 0;
}

.page-heading {
  font-size: 20px;
}

.post-list {
  margin-left: 0;
  list-style: none;

  > li {
    margin-bottom: $spacing-unit;
  }
}

.post-excerpt p {
  font-size: smaller; }

.post-meta {
  font-size: $small-font-size;
  color: $grey-color;
}

.post-link {
  display: block;
  font-size: 24px;
}



/**
 * Posts
 */
.post-header {
  margin-bottom: $spacing-unit;
}

.post-title {
  font-size: 28px;

  @include media-query($on-laptop) {
    font-size: 24px;
  }
  @include media-query($on-palm) {
    font-size: 20px;
  }
}

.post-content {
  margin-bottom: $spacing-unit;

  h2 {
    font-size: 24px;
    margin-top: $spacing-unit;
    margin-bottom: $spacing-unit;

    @include media-query($on-laptop) {
      font-size: 20px;
    }
    @include media-query($on-palm) {
      font-size: 16px;
    }
  }

  h3 {
    font-size: 20px;
    margin-top: $spacing-unit;
    margin-bottom: $spacing-unit;

    @include media-query($on-laptop) {
      font-size: 16px;
    }

    @include media-query($on-palm) {
      font-size: 14px;
    }
  }

  h4 {
    font-size: 16px;
    margin-top: $spacing-unit;
    margin-bottom: $spacing-unit;

    @include media-query($on-laptop) {
      font-size: 14px;
    }

    @include media-query($on-palm) {
      font-size: 12px;
    }
  }
}
